body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.stackScroll::-webkit-scrollbar{  
  width: 5px;   
  height: 10px;
}
.stackScroll::-webkit-scrollbar-track{
  border-radius: 5px;
  box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.25);
}
.stackScroll::-webkit-scrollbar-thumb{
  border-radius: 5px;
  background-color: #616161;
}
.card1{
  display: flex;
  overflow: hidden;
  position: relative;
  background-size: cover ;     
  background-repeat: no-repeat;
} 
.card1{
    width: 145px;
    height: 208px;
    display: inline-block;
    overflow: hidden;
    position: relative;
    background-image: url(/static/media/img1.f9ad9cce.png); 
    background-size: cover ;     
    background-repeat: no-repeat;
    margin-left: 10px;
    cursor: pointer;
}
.card123{
    display: inline-block;
    overflow: hidden;
    position: relative;
    background-size: cover ;     
    background-repeat: no-repeat;
    margin-left: 10px;
    cursor: pointer;
}

.number{
    display: flex;
    position: absolute ;
    background-color: transparent;
    line-height: 0;
    top: 22px;
    left: 13px;
    font-size: 25px; 
    font-weight: 600;
    color: aliceblue;
    font-family: 'Lost Highway', sans-serif;
                                                
}
.position{
    display: flex;
    position: absolute;
    background-color: transparent;
    top: 45px;
    left:17px ;
    line-height: 0;
    font-size: 10px;
    font-weight: 600;
    color: aliceblue;
    font-family: Arial, Helvetica, sans-serif;
    
}
.teamLogo{
    display: flex;
    width: 25px;
    height: 25px;
    position: absolute;
    top: 57.5px;
    left: 13px;
    background-color: transparent;
    background-repeat:no-repeat ;
    background-size: cover ;

    
}
.playerDp{
    display: flex;
    width: 70px;
    height: 70px;
    background-color: transparent;
    background-size: cover ;
    left: 65px;
    top: 5px;
    position: absolute;
    background-repeat: no-repeat;

}
.star{
    position: absolute;
    background-color: transparent;
    top: 70px;
    left: 5px;
  
}
.pleyerName{
    display: flex;
    position: absolute;
    background-color: transparent;
    top: 90px;
    left: 50.5px;
    font-size: 15px;
    font-weight: 500;
    color: aliceblue;
}
.pleyerNameLength{
    display: flex;
    position: absolute;
